import React, {Component} from 'react';
import 'weui';
import './Todolist.css';
import {Switch, Route} from 'react-router-dom';

import Tabbar from './Tabbar';
import New from './New';
import Person from './Person';
import Wait from './Wait';
import ClickContent from './Wait/ClickContent';


class Todolist extends Component{


    render(){
        return(
            <div className='Todolist'>
                <Switch>
                    <Route path='/todolist/person' component={Person}></Route>
                    <Route path='/todolist/new' component={New}></Route>
                    <Route exact path='/todolist' component={Wait}></Route>
                    <Route path='/todolist/wait/clickContent/:id' component={ClickContent}></Route>
                    <Route path='/todolist/tabbar' component={Tabbar}></Route>
                </Switch>
                <Tabbar></Tabbar>
            </div>
        )
    }
}


export default Todolist;