import React, {Component} from 'react';
import 'weui';
import Axios from 'axios';
import './ClickContent.css';
import {Link} from 'react-router-dom';
import qs from 'qs';


class ClickContent extends Component{

    state={
        id:'',
        name:'',
        remind_at:'',
        finish_at:'',
        repeat:'',
        status:'',
        created_at:'',
        updated_at:'',
        history:require('history').createHashHistory,
    }

    componentDidMount(){
        let token=window.localStorage.getItem('token')
        let id=this.props.match.params.id
        this.setState({id:id})
        Axios({
            method:'get',
            url:`https://www.it266.com/app/todo/detail?id=${id}&token=${token}`,
        })
        .then((response)=>{
            // console.log(response.data)
            if(response.data.status){
                this.setState({
                    name:response.data.data.name,
                    remind_at:response.data.data.remind_at,
                    finish_at:response.data.data.finish_at,
                    repeat:response.data.data.repeat,
                    status:response.data.data.status,
                    created_at:response.data.data.created_at,
                })
            }
        })
        .catch((error)=>{
            console.log(error)
        })
    }


    handlerevise=(()=>{
        let token=window.localStorage.getItem('token')
        let id=this.props.match.params.id
        this.setState({id:id})
        Axios({
            method:'post',
            url:`https://www.it266.com/app/todo/update?id=${id}&token=${token}`,
            data:qs.stringify({
                name:this.state.name,
                remind_at:this.state.remind_at,
                repeat:this.state.repeat,
            })
        })
        .then((response)=>{
            // console.log(response.data)
            if(response.data.status){
                this.props.history.push('/todolist')
            }
        })
        .catch((error)=>{
            console.log(error)
        })
    })


    render(){
        let rep = ['不重复','每天','每周','每月','每季度','每年']
        return(
            <div className='ClickContent'>
                <div className='title'>
                    <Link to='/todolist'><img src={require('../../../../imgs/left.png')} alt=''/></Link>
                    <span>事件详情</span>
                </div>
                <div className='aname'>
                    <img src={require('../../../../imgs/tab.png')} alt=''/>
                    <input 
                        type='text' 
                        value={this.state.name}
                        onChange={(e)=>{
                            this.setState({name:e.target.value})
                        }}
                     />
                </div>
                <div className='time'>
                    <img src={require('../../../../imgs/wait2.png')} alt=''/>
                    <span>修改提醒时间 </span>
                    <input 
                        type='text'
                        value={this.state.remind_at}
                        onChange={(e)=>{
                            this.setState({remind_at:e.target.value})
                        }}
                    />
                </div>
                <div className='repreat'>
                    <img src={require('../../../../imgs/repreat.png')} alt=''/>
                    <span>重复设置：{rep[this.state.repeat]}</span>
                </div>
                <div className='ok'>
                    <img src={require('../../../../imgs/ok2.png')} alt=''/>
                    <span>是否完成: {this.state.status===1?'未完成':'已完成'}</span>
                </div>
                <div className='finish'>
                    <img src={require('../../../../imgs/wait2.png')} alt=''/>
                    <span>创建时间: {this.state.created_at}</span>
                </div>

                <div className='btn-save' onClick={this.handlerevise}><button>保存修改</button></div>
            </div>
        )
    }
}

export default ClickContent;