import React, {Component} from 'react';
import 'weui';
import {Link} from 'react-router-dom';
import './Login.css';
import Axios from 'axios';
import qs from 'qs';


class Login extends Component{

    state={
        mobile:'',
        password:'',
        access_key:'821fd971d3a8b4cb5f34290fb93b1302',
        errormessage:'',
        history:require('history').createHashHistory,
    }

    handleLogin=(()=>{
        //登录请求
        // let token=window.localStorage.getItem('token')
        Axios({
            method:'post',
            url:`https://www.it266.com/api/customer/token?access_key=${this.state.access_key}`,
            data:qs.stringify({
                mobile:this.state.mobile,
                password:this.state.password,
            })
        })
        .then((response)=>{
            // console.log(response.data)
            if(response.data.status){
                alert('登陆成功')
                window.localStorage.setItem('token',response.data.data.token)
                this.props.history.push('/todolist')
            }else{
                this.setState({errormessage:response.data.data})
            }
        })
        .catch((error)=>{
            console.log(error)
        })
    })


    render(){
        return(
            <div className='Login'>
                <div className='title'>
                    <img src={require('../../imgs/photo.png')} alt=''/>
                    <span>Welcome to Login</span>
                </div>
                <div className='register'>
                    <span>没有账号？</span>
                    <Link to='/register'>点此注册</Link>
                </div>
                <div className='account'>
                    <img src={require('../../imgs/phone.png')} alt=''/>
                    <input type='text' maxLength='11' placeholder='请输入手机号'
                        onChange={(e)=>{
                            this.setState({mobile:e.target.value})
                        }}
                    />
                </div>
                <div className='password'>
                    <img src={require('../../imgs/lock.png')} alt=''/>
                    <input type='password' placeholder='请输入密码'
                        onChange={(e)=>{
                            this.setState({password:e.target.value})
                        }}
                    />
                </div>
                <div className='errormessage'>{this.state.errormessage}</div>
                <div className='point'>登录即表示同意《用户注册协议》与《隐私协议》</div>
                <span className='regist'>
                    <span className='regist-font' onClick={this.handleLogin}>点击登录</span>
                    <img src={require('../../imgs/right.png')} alt=''/>
                </span>


                
            </div>
        )
    }
}


export default Login;