import React, {Component} from 'react';
import 'weui';
import './Wait.css';
import Axios from 'axios';
import {Link} from 'react-router-dom';
import qs from 'qs';
// import $ from 'jquery';


class Wait extends Component{

    state={
        page:'',  //当前⻚码 默认1
        status:'',  //状态： 0全部 1未完成 2已完成 默认0
        page_size:'', //每⻚记录数 最⼤100 默认20
        sortby:'id', //排序字段: id、finish_at
        order:'asc',  //排序⽅式: asc 升序 desc 降序
        array:[],
        id:'',
        history:require('history').createHashHistory,
    }


    // 刷新页面请求
    handlepush=(()=>{
        let token=window.localStorage.getItem('token')
        Axios({
            method:'get',
            url:`https://www.it266.com/app/todo?token=${token}&page=1&status=0&page_size=20&sortby=id&order=asc`,
        })
        .then((response)=>{
            // console.log(response.data)
            if(response.data.status){
                this.setState({
                    page:response.data.data.page.page,
                    page_size:response.data.data.page.page_size,
                    array:response.data.data.data,
                })
            }
        })
        .catch((error)=>{
            console.log(error)
        })
    })


    // 页面刚进入的请求
    componentDidMount(){
        this.handlepush()
    }


    // 删除事件请求
    handleremove=((id)=>{
        let token=window.localStorage.getItem('token')
        Axios({
            method:'post',
            url:`https://www.it266.com/app/todo/delete?token=${token}`,
            headers: { 'content-type': 'application/x-www-form-urlencoded' },
            data:qs.stringify({id:id})
        })
        .then((response)=>{
            // console.log(response.data)
            if(response.data.status){
                this.handlepush()
            }
        })
        .catch((error)=>{
            console.log(error)
        })
    })


    // 完成事件请求
    handlefinish=((id)=>{
        let token=window.localStorage.getItem('token')
        Axios({
            method:'post',
            url:`https://www.it266.com/app/todo/finish?token=${token}`,
            headers: { 'content-type': 'application/x-www-form-urlencoded' },
            data:qs.stringify({id:id})
        })
        .then((response)=>{
            // console.log(response.data)
            if(response.data.status){
                this.handlepush()
            }
        })
        .catch((error)=>{
            console.log(error)
        })
    })


    render(){
        return(
            <div className='Wait'>
                <div className='title'>
                    <div className='list'><img src={require('../../../imgs/list.png')} alt=''/></div>
                    <div className='title-font'>待办事件列表</div>
                    <div className='more'><img src={require('../../../imgs/more.png')} alt=''/></div>
                    {/* <img src='' alt=''/> */}
                </div>

                <div className='no-finish'>未完成</div>
                <div className='success'>
                    {
                        this.state.array.map((item,index)=>{
                            if(item.status===1){
                                return(
                                    <div className='weui-cell content' key={index}>
                                        <div className='border'><img src={require('../../../imgs/tab.png')} alt=''/></div>
                                        <Link to={`/todolist/wait/clickContent/${item.id}`} className='click'>
                                            <div className='content-name'>{item.name}</div>
                                            <div className='click-message'>
                                                {/* <div>{item.}</div> */}
                                                <div>{item.repeat}</div>
                                            </div>
                                        </Link>
                                        <div className='list-finish'
                                            onClick={()=>{
                                                this.handlefinish(item.id)
                                            }}
                                        >完成</div>
                                        <div className='move' onClick={()=>{
                                            this.handleremove(item.id)
                                        }}><img src={require('../../../imgs/del.png')} alt=''/></div>
                                    </div>
                                    )
                                }

                        })
                    }
                </div>
                <div className='ok-finish'>已完成</div>
                <div className='no-success'>
                    {
                        this.state.array.map((item,index)=>{
                            if(item.status===2){
                                return(
                                    <div className='weui-cell content' key={index}>
                                        <div className='border'><img src={require('../../../imgs/tab.png')} alt=''/></div>
                                        <Link to={`/todolist/wait/clickContent/${item.id}`} className='click'>
                                            <div className='content-name'>{item.name}</div>
                                            <div className='click-message'>
                                                {/* <div>{item.}</div> */}
                                                <div>{item.repreat}</div>
                                            </div>
                                        </Link>
                                        <div className='move' onClick={()=>{
                                            this.handleremove(item.id)
                                        }}><img src={require('../../../imgs/del.png')} alt=''/></div>
                                    </div>
                                )
                            }

                        }) 
                    }
                </div>
                
            </div>
        )
    }
}

export default Wait;