import React, {Component} from 'react';
import 'weui';
import Axios from 'axios';
import qs from 'qs';
import './New.css';
import {Link} from 'react-router-dom';


class New extends Component{

    state={
        name:'',   //事件说明
        remind_at:'', //提醒时间
        repeat:'', //重复(0不重复 1每天 2每周 3每⽉ 4每季度 5每年)
        date:'',  //年月日
        time:'',  //时分
    }

    handlesave=(()=>{
        let token=window.localStorage.getItem('token')
        Axios({
            method:'post',
            url:`https://www.it266.com/app/todo/create?token=${token}`,
            data:qs.stringify({
                name:this.state.name,
                remind_at:`${this.state.date} ${this.state.time}:00`,
                repeat:this.state.repeat,
            })
        })
        .then((response)=>{
            // console.log(response.data)
            if(response.data.status){
                this.props.history.push('/todolist')
            }
        })
        .catch((error)=>{
            console.log(error)
        })
    })


    render(){
        return(
            <div className='New'>
                <div className='title'>
                    <Link to='/todolist'><img src={require('../../../imgs/left.png')} alt=''/></Link>
                    <span>新增事件</span>
                </div>
                <div className='aname'>
                    <img src={require('../../../imgs/tab.png')} alt=''/>
                    <input type='text' placeholder='name:事件名'
                        onChange={(e)=>{
                            this.setState({name:e.target.value})
                        }}
                    />
                </div>
                <div className='time'>
                    <img src={require('../../../imgs/wait2.png')} alt=''/>
                    <span>提醒时间 </span>
                    <input type='date'
                        onChange={(e)=>{
                            // console.log(e.target.value)
                            this.setState({date:e.target.value})
                        }}
                    />
                    <input type='time'
                        onChange={(e)=>{
                            // console.log(e.target.value)
                            this.setState({time:e.target.value})
                        }}
                    />
                </div>
                <div className='repreat' style={{display:'flex'}}>
                    <img src={require('../../../imgs/repreat.png')} alt=''/>
                    <div>
                        <label><input type='radio' name='repeat' value='0' 
                            onChange={(e)=>{
                                this.setState({repeat:e.target.value})
                        }}/>不重复</label>
                        <label><input type='radio' name='repeat' value='1'
                            onChange={(e)=>{
                                this.setState({repeat:e.target.value})
                        }}/>每天</label>
                        <label><input type='radio' name='repeat' value='2'
                            onChange={(e)=>{
                                this.setState({repeat:e.target.value})
                        }}/>每周</label><br/>
                        <label><input type='radio' name='repeat' value='3'
                            onChange={(e)=>{
                                this.setState({repeat:e.target.value})
                        }}/>每月</label>
                        <label><input type='radio' name='repeat' value='4'
                            onChange={(e)=>{
                                this.setState({repeat:e.target.value})
                        }}/>每季度</label>
                        <label><input type='radio' name='repeat' value='5'
                            onChange={(e)=>{
                                this.setState({repeat:e.target.value})
                        }}/>每年</label>
                    </div>
                </div>
                <div className='save'><button onClick={this.handlesave}>保存</button></div>

                {/* 设置重复选项 */}
                <div className='set-repreat' style={{display:'none'}}>
                    <div className='set-repreat-back'>
                        <div>
                            <div>设置重复</div>
                            <img src='' alt=''/>
                        </div>
                        <div>

                        </div>
                    </div>
                </div>
            </div>
                
        )
    }
}


export default New;