import React, {Component} from 'react';
import 'weui';
import {Link} from 'react-router-dom';
import './Register.css';
import Axios from 'axios';
import qs from 'qs';


class Register extends Component{
    
    state={
        captchaId:'hidden',  //图片验证页面
        mobile:'',
        errormessage:'', //手机号输入错误信息反馈
        captchakey:'',  //图片验证内容
        url:'',  //图片验证地址
        captcha_code:'', //输入的图片验证内容
        captchaerror:'', //图片验证码错误信息
        time:60,
        getyzm:'获取验证码',
        disabled:'',
        verification_key:'',  //系统发送的短信验证码
        verification_code:'',  //输入的短信验证内容
        access_key:'821fd971d3a8b4cb5f34290fb93b1302',
        password:'',
        nickname:'',
        registererror:'',
        history:require('history').createHashHistory,
        nicknameError:'',
        mobileError:'',
        passwordError:'',
    }

    //图片请求
    handleCaptcha=(()=>{
        Axios({
            method:'post',
            url:'https://www.it266.com/api/image/captcha',
            headers: { 'content-type': 'application/x-www-form-urlencoded' },
        })
        .then((response)=>{
            // console.log(response.data)
            if(response.status){
                this.setState({captchakey:response.data.data.captcha_key,
                    url:response.data.data.url})
            }
        })
        .catch((error)=>{
            console.log(error)
        })
    })

    //判断输入的图片验证码是否正确
    handleCaptchaCode=(()=>{
       
        Axios({
            method:'post',
            url:'https://www.it266.com/api/sms/verification',
            headers: { 'content-type': 'application/x-www-form-urlencoded' },
            data:qs.stringify({
                access_key:'821fd971d3a8b4cb5f34290fb93b1302',
                mobile:this.state.mobile,
                captcha_code:this.state.captcha_code,
                captcha_key:this.state.captchakey,})
        })
        .then((response)=>{
            // console.log(response.data)
            if(response.data.status){
                this.setState({captchaId:'hidden',verification_key:response.data.data.verificationKey},this.run())
            }else{
                this.setState({captchaerror:response.data.data})
            }
        })
        .catch((error)=>{
            console.log(error)
        })
    })

    run=(()=>{
        this.timer=setInterval(()=>{
            this.setState({time:this.state.time-1,getyzm:this.state.time+'s', disabled:'disabled'})
            if(this.state.time===-1){
                clearInterval(this.timer)
                this.setState({time:59,getyzm:'重新发送',disabled:''})
            }
        },1000)
       
    })

    componentWillUnmount(){
        clearInterval(this.timer)
    }


    //账户注册请求
    handleRegister=(()=>{
        Axios({
            method:'post',
            url:`https://www.it266.com/api/customer/create?access_key=${this.state.access_key}`,
            headers: { 'content-type': 'application/x-www-form-urlencoded' },
            data:qs.stringify({
                mobile:this.state.mobile,
                password:this.state.password,
                nickname:this.state.nickname,
                verification_key:this.state.verification_key,
                verification_code:this.state.verification_code,
            })
        })
        .then((response)=>{
            // console.log(response.data)
            if(response.data.status){
                alert('注册成功')
                this.props.history.push('/')
                // window.localStorage.setItem('token',response.data.data.token)
            }else{
                this.setState({register:response.data.data})
            }
        })
        .catch((error)=>{
            console.log(error)
        })
    })


    render(){
        return(
            <div className='Register'>
                <div className='title'>
                    <img src={require('../../imgs/photo.png')} alt=''/>
                    <span>Welcome to Register</span>
                </div>
                <div className='login'>
                    <span>已有账号？</span>
                    <Link to='/'>点此登录</Link>
                </div>
                <div className='nickname'>
                    <img src={require('../../imgs/me.png')} alt=''/>
                    <input type='text' placeholder='输入昵称'
                        onChange={(e)=>{
                            this.setState({nickname:e.target.value})
                        }}
                        onBlur={()=>{
                            if(this.state.nickname.length<1){
                                this.setState({nicknameError:'昵称不能为空'})
                            }
                        }}
                    />
                </div>
                <div className='nicknameError'>{this.state.nicknameError}</div>
                <div className='account'>
                    <img src={require('../../imgs/phone.png')} alt=''/>
                    <input type='text' maxLength='11' placeholder='请输入手机号'
                        onChange={(e)=>{
                            this.setState({mobile:e.target.value})
                        }}
                        onBlur={()=>{
                            if(this.state.mobile.length<1){
                                this.setState({mobileError:'手机号不能为空'})
                            }
                        }}
                    />
                </div>
                <div className='mobileError'>{this.state.mobileError}</div>
                <div className='error'>{this.state.errormessage}</div>
                <div className='account'>
                    <img src={require('../../imgs/lock.png')} alt=''/>
                    <input type='password' placeholder='请输入密码'
                        onChange={(e)=>{
                            this.setState({password:e.target.value})
                        }}
                        onBlur={()=>{
                            if(this.state.password.length<1){
                                this.setState({passwordError:'密码不能为空'})
                            }
                        }}
                    />
                </div>
                <div className='passwordError'>{this.state.passwordError}</div>
                <div className='password'>
                    <img src={require('../../imgs/key.png')} alt=''/>
                    <input type='text' placeholder='请输入验证码'
                        onChange={(e)=>{
                            this.setState({verification_code:e.target.value})
                        }}
                    />
                    <button 
                        disabled={this.state.disabled}
                        className='code' 
                        onClick={()=>{
                            if(!(/^1[0-9]{10}$/.test(this.state.mobile))){
                                this.setState({errormessage:'请输入正确的手机号'})
                            }else{
                                this.handleCaptcha()
                                this.setState({captchaId:'block'})
                            }
                        }}>
                        {this.state.getyzm}
                    </button>
                </div>
                <div className='registererror'>{this.state.registererror}</div>
                <div className='point'>注册即表示同意《用户注册协议》与《隐私协议》</div>
                <span className='regist'>
                    <span className='regist-font'
                        onClick={this.handleRegister}
                    >点击注册</span>
                    <img src={require('../../imgs/right.png')} alt=''/>
                </span>


                {/* 图片提交页面 */}
                <div className='captcha' id={this.state.captchaId}>
                    <div className='captcha-bor'>
                        <div className='safe'>图片验证</div>
                        <div className='captcha-capt'>
                            <div className='picture'><img src={this.state.url} alt=''/></div>
                            <button onClick={()=>{
                                this.handleCaptcha()
                            }}>换一换</button>
                        </div>
                        <input type='text' placeholder='请输入验证码' onChange={(e)=>{
                            this.setState({captcha_code:e.target.value})
                        }}/>
                        <div className='captchaerror'>{this.state.captchaerror}</div>
                        <div className='ok'>
                            <button onClick={this.handleCaptchaCode}>确认</button> 
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


export default Register;