import React, {Component} from 'react';
import 'weui';
import './Tabbar.css';
import {Link} from 'react-router-dom';


class Tabbar extends Component{
 

    render(){
        return(
            <div className='Tabbar'>
                <div className="weui-tabbar">
                    <div className="weui-tabbar__item">
                        <Link to='/todolist'>
                            <div style={{display:'inline-block', position:'relative'}}>
                                <img src={require('../../../imgs/wait2.png')} alt="" className="weui-tabbar__icon"/>
                            </div>
                            <p className='weui-tabbar__label'>待办</p>
                        </Link>
                    </div>
                    <div className="weui-tabbar__item">
                        <Link to='/todolist/new'>
                            <div style={{display:'inline-block', position:'relative'}}>
                                <img src={require('../../../imgs/new2.png')} alt="" className="weui-tabbar__icon"/>
                            </div>
                            <p className='weui-tabbar__label'>新增</p>
                        </Link>
                    </div>
                    <div className="weui-tabbar__item">
                        <Link to='/todolist/person'>
                            <div style={{display:'inline-block', position:'relative'}}>
                                <img src={require('../../../imgs/my2.png')} alt="" className="weui-tabbar__icon"/>
                            </div>
                            <p className='weui-tabbar__label'>我的</p>
                        </Link>
                    </div>
                </div>
            </div>
        )
    }
}


export default Tabbar;