import React, {Component} from 'react';
import 'weui';
import './Person.css';
import Axios from 'axios';


class Person extends Component{

    state={
        mobile:'',
        nickname:'',
    }

    componentDidMount(){
        //个人信息请求
        let token=window.localStorage.getItem('token')
        Axios({
            method:'get',
            url:`https://www.it266.com/api/customer/whoami?token=${token}`,
        })
        .then((response)=>{
            // console.log(response.data)
            if(response.data.status){
                this.setState({mobile:response.data.data.mobile,nickname:response.data.data.nickname})
            }
        })
        .catch((error)=>{
            console.log(error)
        })
    }

    render(){
        return(
            <div className='Person'>
                <div className='header-img'><img src={require('../../../imgs/header1.png')} alt=''/></div>
                <div className='name'>Hello! {this.state.nickname}</div>
                <div className='Person-message'>
                    <div className='tel'>
                        <img src={require('../../../imgs/phone1.png')} alt=''/>
                        <span>手机号</span>
                    </div>
                    <div className='num'>
                        <span>{this.state.mobile}</span>
                        <img src={require('../../../imgs/right1.png')} alt=''/>
                    </div>
                </div>
                <div className='Person-message'>
                    <div className='tel'>
                        <img className='many' src={require('../../../imgs/many.png')} alt=''/>
                        <span>当前登录设备1台</span>
                    </div>
                    <div className='num'>
                        <span className='num-font'>升级多设备支持</span>
                        <img src={require('../../../imgs/right1.png')} alt=''/>
                    </div>
                </div>
                <div className='Person-message'>
                    <div className='tel'>
                        <img src={require('../../../imgs/say.png')} alt=''/>
                        <span>跨平台与数据同步</span>
                    </div>
                    <div className='num'>
                        <span>云同步、桌面端</span>
                        <img src={require('../../../imgs/right1.png')} alt=''/>
                    </div>
                </div>
                <div className='Person-message'>
                    <div className='tel'>
                        <img src={require('../../../imgs/event.png')} alt=''/>
                        <span>事件设置</span>
                    </div>
                    <div className='num'>
                        <span></span>
                        <img src={require('../../../imgs/right1.png')} alt=''/>
                    </div>
                </div>
                <div className='Person-message'>
                    <div className='tel'>
                        <img src={require('../../../imgs/about.png')} alt=''/>
                        <span>关于</span>
                    </div>
                    <div className='num'>
                        <span className='num-font'>版本、关于我们</span>
                        <img src={require('../../../imgs/right1.png')} alt=''/>
                    </div>
                </div>
                <div className='Person-message'>
                    <div className='tel'>
                        <img src={require('../../../imgs/exit.png')} alt=''/>
                        <span className='exit' onClick={()=>{
                            localStorage.removeItem('token');
                            this.props.history.push('/')
                        }}>退出登录</span>
                    </div>
                    <div className='num'>
                        <span></span>
                        <img src={require('../../../imgs/right1.png')} alt=''/>
                    </div>
                </div>
            </div>
        )
    }
}


export default Person;